.about-header {
    text-align: center;
    font-size: 2.5rem;
    letter-spacing: 3px;
    
    text-shadow: 2px 2px 2px rgb(177, 176, 176);
    font-family: 'Poppins', sans-serif;
    color: rgb(100, 100, 250);
    margin-bottom: 80px;
}

.about-title {
    text-align: center;
    padding: 20px 0;
    margin-top: 40px;
    width: 100%;
    font: 3rem;
    letter-spacing: 1.3px;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;

}

.about-title-right {
    text-align: center;
    padding: 20px 0;
    margin-top: 40px;
    width: 100%;
    font: 3rem;
    letter-spacing: 1.3px;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
}

.about-text {
    font-family: 'Domine', serif;
    font-size: 1.1rem;
    line-height: 1.4;
    text-align: center;
    width: 100%;
    margin-bottom: 40px;
    padding: 0 3%;
}

.about-text-middle {
    font-family: 'Domine', serif;
    font-size: 1.1rem;
    line-height: 1.4;
    text-align: center;
    width: 100%;
    margin-bottom: 40px;
    padding: 0 3%;
}

.left-image {
    box-shadow: 2px 2px 15px rgb(151, 149, 149);
    width: 35%;
    height: 400px;
    margin-right: 85px;
    margin-bottom: 50px;
}

.right-image {
    box-shadow: 2px 2px 15px rgb(151, 149, 149);
    width: 35%;
    height: 400px;
    margin-left: 85px;
    margin-bottom: 50px;
}

.about-container {
    margin-top: 40px;
    width: 100%;
    display: flex;
    flex-direction: row;

}

.desktop-section {
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-top: 40px;
}

.mobile-section {
    width: 100%;
    display: flex;
    flex-direction: row;
    
    display: none;
}

@media(max-width:600px) {
    .about-container {
        flex-direction: column;
    }

    .about-header{
        margin-top: 30px;
        margin-bottom: 20px;
    }

    .about-text {
        padding: 0 6vw;
        margin-bottom: 30px;
    }

    .title-right {
        display: block;
    }

    .left-image {
        margin: 20px 10vw;
        width: 80vw;
    }

    .about-title {
        margin-top: 10px;
        margin-bottom: 10px;
    }

    .desktop-section {
        display: none;
    }

    .mobile-section {
        display: block;
    }

}